.start__wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  column-gap: 3rem;
}

.start__img,
.start__content {
    width: 50%;
}
.start__img img{
    width: 100%;
}

.start__content h2{
    font-size: 2.5rem;
    margin-bottom: 20px;
}

.start__content p{
    font-size: 1.1rem;
    color: var(--heading-color);
    line-height: 28px;
    margin-bottom: 50px;
}